  80% {
    clip: rect(0, 300px, 100px, 0);
    opacity: 0; }
  90% {
    clip: rect(0, 300px, 100px, 0);
    opacity: 0; }
  100% {
    clip: rect(0, 300px, 100px, 0);
    opacity: 0; } }

@keyframes pulse {
